export default {
  namespaced: true,//命名空间
  state: {
    // 收藏
    Favorites: [],
    // 购物车
    ShopcartList: []
  },
  mutations: {
    //收藏添加
    FavoritesAdd(state, obj) {
      state.Favorites.push(obj);
      for (var i = 0; i < state.Favorites.length; i++) {
        if (state.Favorites[i] == '') {
          state.Favorites.splice(i, 1)
        }
        for (var j = i + 1; j < state.Favorites.length; j++) {
          if (state.Favorites[i].goods_name == state.Favorites[j].goods_name) {
            state.Favorites.splice(j, 1);
            j--;
          }
        }
      }

      let arr = JSON.parse(localStorage.getItem("Favorites"))
        for (let k = 0; k < arr.length; k++) {
          if (arr[k].goods_id == obj.goods_id) {
            obj.sc_isActive = !obj.sc_isActive
            arr[k].sc_isActive == obj.sc_isActive
          }
      }
      localStorage.setItem("Favorites", JSON.stringify(state.Favorites));

      // Replace
    },
    //收藏删除
    FavoritesDetail(state, index) {
      state.Favorites.splice(index, 1);
      localStorage.setItem("Favorites", JSON.stringify(state.Favorites));
    },
    //购物车添加
    ShopcartListAdd(state, obj) {
      state.ShopcartList.push(obj);
      for (var i = 0; i < state.ShopcartList.length; i++) {
        if (state.ShopcartList[i].goods_name == obj.goods_name) {
          state.ShopcartList[i] = obj
        }
        for (var j = i + 1; j < state.ShopcartList.length; j++) {
          if (state.ShopcartList[i].goods_name == state.ShopcartList[j].goods_name) {
            state.ShopcartList.splice(j, 1);
            j--;
          }
        }
      }
      
      localStorage.setItem("Shopcart", JSON.stringify(state.ShopcartList));
    },
    //购物车删除
    ShopcartListDetail(state, index){
      state.ShopcartList.splice(index, 1);
      localStorage.setItem("Shopcart", JSON.stringify(state.ShopcartList));
    }
  },
};