export const routes = [
  {
    path: "/",
    redirect: "/advertpage"//默认进入广告页
  },
  {
    path: '/main',
    name: 'Main',
    component: r => require(['../views/Main.vue'], r),
    children: [
      {
        path: 'home',
        name: 'Home',
        component: r => require(['../views/mainViews/Home.vue'], r)
      },
      {
        path: 'category',
        name: 'Category',
        component: r => require(['../views/mainViews/Category.vue'], r)
      },
      {
        path: 'shopcart',
        name: 'Shopcart',
        component: r => require(['../views/mainViews/Shopcart.vue'], r)
      },
      {
        path: 'my',
        name: 'My',
        component: r => require(['../views/mainViews/My.vue'], r)
      }
    ]
  },
  {
    path: '/search',
    name: 'Search',
    component: r => require(['../views/Search.vue'], r)
  },
  {
    path: '/detail',
    name: 'Detail',
    component: r => require(['../views/Detail.vue'], r)
  },
  {
    path: '/search',
    name: 'Search',
    component: r => require(['../views/Search.vue'], r)
  },
  {
    path: '/login',
    name: 'Login',
    component: r => require(['../views/Login.vue'], r)
  },
  {
    path: '/register',
    name: 'Register',
    component: r => require(['../views/Register.vue'], r)
  },
  {
    path: '/account',
    name: 'Account',
    component: r => require(['../views/Account.vue'], r)
  },
  {
    path: '/forgot',
    name: 'Forgot',
    component: r => require(['../views/Forgot.vue'], r)
  },
  {
    path: '/myAddress',
    name: 'MyAddress',
    component: r => require(['../views/MyAddress.vue'], r)
  },
  {
    path: '/address/:aid?',
    name: 'Address',
    component: r => require(['../views/Address.vue'], r)
  },
  {
    path: '/order',
    name: 'Order',
    component: r => require(['../views/Order.vue'], r),
    children: [
      {
        path: "orderAll",
        name: "OrderAll",
        component: () => import("../components/orderList/OrderAll.vue")
      },
      {
        path: "orderPay",
        name: "OrderPay",
        component: () => import("../components/orderList/OrderPay.vue")
      },
      {
        path: "orderGoods",
        name: "OrderGoods",
        component: () => import("../components/orderList/OrderGoods.vue")
      },
      {
        path: "orderEnd",
        name: "OrderEnd",
        component: () => import("../components/orderList/OrderEnd.vue")
      },
    ]
  },
  {
    path: '/favorites',
    name: 'Favorites',
    component: r => require(['../views/Favorites.vue'], r)
  },
  {
    path: '/pay/:order/:total',
    name: 'Pay',
    component: r => require(['../views/Pay.vue'], r)
  },
  {
    path: "/guidepage",
    name: "Guidepage",
    component: r => require(['../views/Guidepage.vue'], r)
  },
  {
    path: "/advertpage",
    name: "Advertpage",
    component: r => require(['../views/Advertpage.vue'], r),
    beforeEnter: (to, from, next) => {
      if (localStorage.advertpage) {
        next();
      } else {
        localStorage.advertpage = true;
        next("/guidepage");
      }
    }
  },
]