<script>
export default {
  created() {
    let a = JSON.parse(localStorage.getItem("Favorites"));
    let b = JSON.parse(localStorage.getItem("Shopcart"));
    if(a == null || b == null){
      localStorage.setItem("Favorites", "[]");
      localStorage.setItem("Shopcart", "[]");
    }else{
      return
    }
  },
};
</script>
