import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import VueAxios from 'vue-axios'
import axios from "axios";

import {
  Button,
  NavBar,
  Form,
  Field,
  Checkbox,
  CheckboxGroup,
  Icon,
  Col,
  Toast,
  Row,
  ShareSheet,
  Cell, 
  CellGroup,
  Tabbar, 
  TabbarItem,
  Swipe, 
  SwipeItem,
  Search,
  Grid, 
  GridItem,
  Lazyload,
  Stepper,
  GoodsAction,
  goodsActionIcon,
  goodsActionButton,
  Empty,
  SwipeCell,
  SubmitBar,
  List,
  AddressEdit,
  AddressList,
  Divider,
  Popup,
  Tabs,
  Tab,
  Uploader,
  NoticeBar,
  ActionSheet,
  Card,
  CountDown
} from 'vant'
Vue.config.productionTip = false

//导入用于设置 rem 基准值模块，默认情况下，设置在iphone6屏幕下的html元素的字体大小为37.5px
import 'lib-flexible';

//导入font-awesome字体图标库
import 'font-awesome/css/font-awesome.min.css'
//注册vant框架的组件
Vue.use(Form)
.use(NavBar)
.use(Button)
.use(Field)
.use(Checkbox)
.use(CheckboxGroup)
.use(Icon)
.use(Col)
.use(Row)
.use(Toast)
.use(ShareSheet)
.use(Cell)
.use(CellGroup)
.use(Tabbar)
.use(TabbarItem)
.use(Swipe)
.use(SwipeItem)
.use(Search)
.use(Grid)
.use(GridItem)
.use(Lazyload)
.use(Stepper)
.use(GoodsAction)
.use(goodsActionIcon)
.use(goodsActionButton)
.use(Empty)
.use(SwipeCell)
.use(SubmitBar)
.use(List)
.use(AddressEdit)
.use(AddressList)
.use(Divider)
.use(Popup)
.use(Tabs)
.use(Tab)
.use(Uploader)
.use(NoticeBar)
.use(ActionSheet)
.use(Card)
.use(CountDown)
Vue.use(VueAxios, axios)
Vue.prototype.$axios = axios;

//配置请求基础路径
axios.defaults.baseURL = 'https://api-hmugo-web.itheima.net/api/public/v1/'

//配置post请求头
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';

//发起请求之前处理
axios.interceptors.request.use(config => {
  if (config.method == 'post') {
    let paramsString = '';
    //post请求参数序列化，转成一个字符串
    for (let key in config.data) {
      paramsString += key + '=' + config.data[key] + '&'
    }
    config.data = paramsString.slice(0, -1);
  }

  return config;
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
